.movie-item {
  flex-basis: 22%;
  display: flex;
  flex-direction: column;
  list-style: none;
  box-sizing: border-box;
  margin: 1.5%;
  border: 1px solid #eee;
  box-shadow: 0 10px 28px -7px rgba(0, 0, 0, 0.1);
}

.movie-item img {
  width: 100%;
}

ul {
  display: flex;
  padding: 20px;
}

.movie-description {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 10px;
}

.movie-description h2 {
  color: #555;
  font-weight: bold;
  margin-bottom: 20px;
}

.movie-details {
  display: flex;
  justify-content: space-between;
}

.movie-details span {
  color: #555;
  font-size: 0.8rem;
  font-weight: bold;
}

.movie-year,
.movie-rating {
  display: flex;
  flex-direction: column;
}

.movie-year .title,
.movie-rating .title {
  color: #aaa;
  margin-bottom: 5px;
  font-size: 0.65rem;
  font-weight: normal;
}

.movie-rating {
  align-items: flex-end;
}
